/*-------------------------*/
/* assignment
---------------------------*/
.topNav{
  padding: 3.2rem;
  .topNavOuter{

  }
  .title{
    @include fontSize(2.4rem , 2.9rem);
    margin-right: 4.6rem;
  }
}

.dropdown{
  .assignmentBtn {
    @include fontSize(1.6rem , 2.4rem);
    padding: 0;
    padding-right: 3.1rem;
    margin-top: 1.2rem;
    &:after {
        display: none;
    }
  }
  .assignmentBtn-icon {
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      color: $secondary;
  }
  .dropdown-item{
    @include fontSize(1.6rem , 2.4rem);
  }
}

.materialGroup{
  .material-search{
    margin-right: 2.6rem;
  }
}

//
.assignmentContent{
  padding: 0 3.2rem;
  .comments{
    @include fontSize(1.2rem , 1.5rem);
    color: $text-gray;
  }
  .content-list{
    padding-left: 0;
    padding-right: 1.6rem;
  }
  .content-comments{
    padding-left: 0;
    padding-right: 0;
  }
}
.list-group-item {
  padding: 1.2rem ;
  border: 0.1rem solid $thirdly;
}

.list-group-item.active {
  color: $primary-dark;
  border: 0.1rem solid $thirdly;
}

.chapter{
  .chapterNum{
    @include fontSize(1.2rem , 1.5rem);
    margin-bottom: 0.4rem;
  }
  .chapterTheme{
    @include fontSize(1.6rem , 1.9rem);
    font-weight: 700;
  }
}
.infomation{
  .infomationDate{
    @include fontSize(1.2rem , 1.5rem);
    margin-bottom: 0.8rem;
  }
  .infomationName{
    @include fontSize(1.2rem , 1.5rem);
    color: $text-gray;
  }
}

.card{
  border: 0.1rem solid $thirdly;
  .card-header{
    background-color: $primary ;
    padding: 1.2rem 1.6rem;
    .chapterNum{
      @include fontSize(1.2rem , 1.5rem);
      margin-bottom: 0.4rem;
    }
    .chapterTheme{
      @include fontSize(1.6rem , 1.9rem);
      font-weight: 700;
    }
  }
  .card-body{
    padding: 1.6rem 3.2rem 2.2rem 1.6rem;
  }
  //01
  .media{
    img {
      @include width(4rem , 4rem);
      border-radius: 100%;
    }
    .medianame{
      @include fontSize(1.6rem , 1.9rem);
      margin-bottom: 0.4rem;
      font-weight: 700;
    }
    .mediadate{
      @include fontSize(1.2rem , 1.5rem);
      color: $text-gray;
    }
    .media-text{
      @include fontSize(1.4rem , 2.1rem);
    }
    code{
      color: $secondary;
      background-color: $primary;
    }
    .downloadBtn{
      max-width: 17.6rem;
      background-color: $primary;
      padding: 0.8rem 1.2rem;
      border: 0.1rem solid $thirdly;
      margin-bottom: 1.6rem;
      &:hover{
        color: $secondary;
      }
    }
    .downloadBtntext{
      @include fontSize(1.6rem , 1.9rem);
      font-weight: 700;
    }
    hr{
      border-top: 0.1rem solid $thirdly;
      margin: 0;
    }
    //02
    ul{
      @include fontSize(1.4rem , 2.1rem);
      margin-left: 3.5rem;
    }
    li{
      list-style: disc;
    }
  }
}

//button
.paginationGroup{
  background-color: $white;
  .material-left{
    margin-right: 2.3rem;
  }
  .btn{
    @include fontSize(1.6rem , 1.9rem);
    padding: 0.8rem 3.2rem;
  }
}

.replyGroup{
  width: 0%;
  height: 0rem;
  max-height: 100%;
  overflow: hidden;
  background-color: $white;
  border: 0rem solid $thirdly;
  position: absolute;
  z-index: 99;
  right: 0;
  bottom: -2.5rem;
  transition: all 1.5s;
  &.active{
    width: 100%;
    height: 33.3rem;
    right: 0;
    bottom: -2.5rem;

    border: 0.1rem solid $thirdly;
  }
  .bodyText{
    border-bottom: 0.1rem solid $thirdly;
    background-color: $primary;
    padding: 1.6rem 2.5rem;
  }
  select {
    @include fontSize(1.6rem , 1.9rem);
    font-weight: bold;
    margin-right: 2.4rem;
    background-color: transparent;
  }
  li{
    margin-right: 3rem;
  }
  .material-icons{
    line-height: 2.9rem;
  }
  .material-left{
    margin-right: 0rem;
  }
  textarea{
    color: #00000062;
    width: 100%;
    height: 21.2rem;
    padding: 1.6rem 2.4rem; 
    font-weight: 300;
    resize: none;
    overflow-y: auto;
  }
  .pagination-function{
    padding: 0 2.4rem;
  }
}
//focus
.btn-secondary:focus, .btn-secondary.focus {

  -webkit-box-shadow: 0 0 0 0.0rem rgba(74, 150, 111, 0.5);
  box-shadow: 0 0 0 0.0rem rgba(74, 150, 111, 0.5);
}
