/*-------------------------*/
/* $Font import 
---------------------------*/
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap');

/*-------------------------*/
/* base
---------------------------*/ 
*:before,
*:after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

* {
    position: relative;
    box-sizing: border-box;
    // outline: 1px solid red;
}
html,
body {
    -webkit-text-size-adjust: none;
    //
    -webkit-tap-highlight-color: transparent;
    //
    background: #fff;
    font-family: "Lato", -apple-system, BlinkMacSystemFont, "Segoe UI", ThegnHei, "Helvetica Neue", Arial, sans-serif;
}
body.active{
    overflow-y: hidden;
    overflow-x: hidden;
}
html {
    font-size: 62.5%;
}
body{
    font-size: 1.6em;
}
img {
    width: 100%;
    height: auto;
    display: block;
    position: relative;
}
h1,h2,h3,h4,h5,h6,p{
    margin: 0;
}
ul,li{
    margin: 0;
    padding: 0;
    list-style: none;
}
a {
    display: inline-block;
    -webkit-touch-callous: none;
    text-decoration: none;
    color: #000;
    text-decoration: none !important;
    //
    &:hover{
        color: $secondary;
    }
}
input {
    -webkit-appearance: none;
    background-color: transparent;
    border: 1px solid #000;
    //ios input
}
input,
select,
textarea {
    outline: none;
    border: none;
    resize: none;
    font-size: initial;
}
.wrap {
    max-width: 128rem;
    height: 100vh;
    margin: auto;
}
.main {
    margin-left: 29.6rem;
}
