/*-------------------------*/
/* sidebar 
---------------------------*/
.sidebar {
    padding: 3.2rem 0rem;
    min-width: 29.6rem;
    background-color: $primary;
    font-weight: bold;
    position: fixed;
    top:0;
    bottom:0;
    h1 {
        width: 100%;
        padding: 0 3.2rem;
        margin-bottom: 3.2rem;
        img {
            max-width: 15.1rem;
        }
    }
    li {
        padding: 0 3.2rem;
        margin-bottom: 2.6rem;
    }
    li.active:before {
        content: "";
        @include width(0.4rem , 3.2rem);
        background: $secondary;
        position: absolute;
        top: 0;
        left: 0;
    }
    li:last-child {
        margin-bottom: 0;
    }
    a{
        @include fontSize(1.6rem , 3.2rem);
        color: $primary-dark;
    }
    li.active a,
    li:hover a{
        color: $secondary;
    }
    span {
        @include fontSize(2.4rem , 3.2rem);
        margin-right: 1.6rem;
    }
}

.user {
    padding: 0 3.2rem;
    img {
        @include width(4rem , 4rem);
        border-radius: 100%;
        margin-right: 1.6rem;
    }
    p {
        @include fontSize(1.6rem , 1.9rem);
    }
    p:first-child {
        margin-bottom: 0.4rem;
    }
    p:last-child {
        @include fontSize(1.4rem , 1.7rem);
        color: $text-gray;
        font-weight: 400;
    }
}
