/*-------------------------*/
/* admin
---------------------------*/
.adminContent{
  padding:  0 3.2rem;
}
.adminBtn {
  @include fontSize(1.6rem , 2.4rem);
  padding: 0;
  padding-left: 3.1rem;
  margin-top: 1.2rem;
  &:after {
      display: none;
  }
}
.adminBtn-icon {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    color: $secondary;
}
//newAddModal
.modalCardList{
  padding: 3.2rem 0rem 2.4rem 3.2rem;
  text-align: left;
  max-height: calc(100vh - 3.5rem);
  overflow: hidden;
  .modal-header{
    padding: 0;
    padding-right: 3.2rem;
    border-bottom: none;
    .modal-title{
      @include fontSize(2.4rem , 2.9rem);
      font-weight: bold;
    }
  }
  .modal-body{
    padding: 0;
    padding-right: 3.2rem;
    overflow-y: auto;
    input{
      border: 0.1rem solid $primary-dark;
      padding: 1.2rem  1.6rem;
    }
    //Verification
    .btn-send{
      @include fontSize(1.6rem , 1.9rem);
      font-weight: bold;
      padding: 1.5rem  0rem;
      border: 0.1rem solid $thirdly;
    }
    hr{
      border-top: 0.1rem solid $thirdly;
      margin-top: 2.4rem;
      margin-bottom: 2.4rem;
    }
    //access
    .btn-access{
      border: 0.1rem solid $primary-dark;
      padding: 1.2rem  1.6rem;
      @include fontSize(1.6rem , 1.9rem);
      color: #0000009A;
      /**/
      appearance:none;
      -moz-appearance:none;
      -webkit-appearance:none;
      background-image: url('../images/expand_more-black-18dp.svg');
      background-position: 96% center;
      background-repeat: no-repeat;
    }
    //Custom Access 
    .form-check{
      .form-check-option{
        display: block;
        position: relative;
        padding-left: 3.5rem;
        margin-bottom: 1.2rem;
        cursor: pointer;
        user-select: none;
        &:last-child{
          margin-bottom: 0rem;
        }
      }
      input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 0;
        width: 0;
        font-weight: 400;
      }
      .checkmark{
        @include width(2rem , 2rem);
        position: absolute;
        border: 0.2rem solid $dark;
        border-radius: 0.2rem;
        top: 0;
        left: 0;
        background-color: $white;
        &:after{
          content: "";
          position: absolute;
          display: none;
          left: 0.5rem;
          top: 0;
          width: 0.7rem;
          height: 1.2rem;
          border: 0.1rem solid $secondary;
          border-width: 0 0.3rem 0.3rem 0;
          transform: rotate(45deg);
        }
      }
    }
  }
  .modal-footer{
    padding: 0;
    padding-right: 3.2rem;
    border: none;
    margin-top: 5rem;
    .btn{
      font-size: 1.6rem;
      font-weight: bold;
      padding: 0.8rem 3.2rem;
    }
  }
}
/*  checkMark checked */
.form-check .form-check-option input:checked ~ .checkmark:after {
  display: block;
}

.table{
  //header
  .admin-thead{
    .admin-thead-tr{

    }
    .admin-thead-th{
      @include fontSize(1.6rem , 1.9rem);
      padding: 1.6rem 1.6rem 0.45rem 0 ;
      font-weight: bold;
      border-top: none;
      border-bottom: 0.1rem solid $primary-dark;
    }
    .material-downward{
      font-size: 1.4rem;
      line-height: 1.8rem;
      color: $secondary;
    }
  }
  //body
  .admin-tbody{
    tr{
      cursor: pointer;
      &:hover{
        background-color: $primary;
      }
    }
    th{
      @include fontSize(1.6rem , 1.9rem);
      border-top: 0.1rem solid $thirdly;
      padding: 1.9rem 0 1.7rem 0 ;
    }
    td{
      padding: 1.9rem 0 1.7rem 0 ;
      border-top: 0.1rem solid $thirdly;
    }
    tr:last-child td,
    tr:last-child th {
      border-bottom: 0.1rem solid $thirdly;
    }
    .button-group{
      padding: 0;
      vertical-align: middle;
      text-align: right;
    }
    .readBtn,.editBtn{
      padding:0
    }
    .readBtn{
      margin-right: 2.5rem;
    }
    .material-icons{
      vertical-align: middle;
    }
  }
}
//readModal
