      object-fit: cover;
    }
  }
  .name{
    font-weight: bold;
  }
  .email{
    color: #0000009A;
  }
  .text{
    padding:0.4rem 0.8rem;
  }
  .modal-footer{
    padding: 0;
    padding-top: 3.2rem;
    border: none;
  }
  .btn{
    @include fontSize(1.6rem , 2.4rem);
    padding: 0.8rem 3.2rem;
  }
  .btn-Deactivate{
  }
  .btn-Edit{

  }
}
//navigation
.navigation{
  margin-top: 6.3rem;
  padding: 0 3.2rem;
  .pagination{
    
  }
  .page-link{
    padding-right:1.1rem;
    padding-left:1.1rem;
    line-height: 1.65;
    color: $dark;
    &.active{
      background-color: $secondary;
      color: $white;
    }
